::-webkit-scrollbar {
  width: 0;
}

*::-webkit-scrollbar {
  height: 3px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: $orange;
}

.container {
  @include container(15);
}

.sr-only {
  @include sr-only();
}

body {
  @include safe-font(Rubik);
  font-weight: 400;
  font-size: 20px;
  line-height: 1.5;
  color: $dark;
  background-color: $bg;
  @include font-media(20, 16);
}

.logo {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  max-width: 200px;
  height: 39px;
  border-radius: 5px;
  transition: opacity 0.3s, box-shadow 0.3s ease-in-out;

  &__img {
    width: 100%;

    @include r(480) {
      width: 115px;
      height: 24px;
    }
  }

  &:hover {
    opacity: 0.8;
  }

  &:focus {
    box-shadow: 0 0 0 2px #ff6838b3;
  }

  &:active {
    box-shadow: 0 0 0 2px #ff673800;
    opacity: 0.5;
  }
}

.link {
  border-radius: 5px;
  transition: color 0.3s, box-shadow 0.3s, text-shadow 0.3s ease-in-out;

  &:hover {
    color: $orange;
  }

  &:focus {
    box-shadow: 0 0 0 2px #ff6838b3;
  }

  &:active {
    box-shadow: 0 0 0 2px #ff673800;
    text-shadow: 2px 2px 5px #31352b80;
  }
}

.link--line {
  color: $darkBlue;
  position: relative;
  border-radius: 5px;

  &:hover,
  &:focus,
  &:active {
    &::after {
      opacity: 0;
    }
  }

  &::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    left: 0;
    bottom: 0;
    background-color: $darkBlue;
    transition: opacity 0.3s ease-in-out;
  }
}

.hero__title {
  font-family: $rubik, $sans-serif;
  font-size: 66px;
  font-weight: 500;
  line-height: 1.3;
  color: $darkBlue;
  text-transform: uppercase;
  @include font-media(66, 40);
}

.title {
  font-weight: 500;
  line-height: 1.3;
  font-size: 36px;
  text-align: center;
  color: $darkBlue;
  @include font-media(36, 24);
}

.title--span {
  display: block;
}

.btn-bg {
  padding: 0;
  line-height: 1;
  color: #fff;
  border-radius: 5px;
  background-color: $orange;
  transition: color 0.3s, box-shadow 0.3s, background-color 0.3s ease-in-out;

  &:hover {
    color: $orange;
    background-color: #ffffff1a;
    box-shadow: 0 5px 12px #ff683833,
      0 0 0 2px #ff6838b3;
  }

  &:focus {
    color: $orange;
    background-color: #ff7a501a;
    box-shadow: 0 0 0 2px #ff6838b3;
  }

  &:active {
    color: $orange;
    background-color: #ff7a501a;
    box-shadow: 0 0 0 2px transparent;
  }
}

.popular,
.restaurant,
.apps {
  padding: 120px 0;

  @include r(480) {
    padding: 40px 0;
  }


  @include r(785) {
    padding: 60px 0;
  }
}